import Swiper, { Navigation } from 'swiper';

export default () => {
  Swiper.use([Navigation]);

  let typesSwiper = new Swiper('.types .swiper-container', {
    slidesPerView: 1,
    speed: 500,
    spaceBetween: 30,
    centeredSlides: true,
    loop: true,
    // centeredSlides: true,
    navigation: {
      nextEl: '.types .swiper-button-next',
      prevEl: '.types .swiper-button-prev',
    },
    breakpoints: {
      // when window width is >= 540px
      540: {
        slidesPerView: 1,
        centeredSlides: true,
        spaceBetween: 30,
      },
      // when window width is >= 768px
      768: {
        slidesPerView: 2,
        centeredSlides: false,
        spaceBetween: 30,
      },
      // when window width is >= 1024px
      1024: {
        slidesPerView: 3,
        centeredSlides: false,
        spaceBetween: 0,
      },
      // when window width is >= 1200px
      1290: {
        slidesPerView: 4,
        centeredSlides: false,
        spaceBetween: 0,
      },
    },
  });
};
