export default () => {
  let showNumber = [];
  let offsetArray = $('.advantages__block');
  //Узнаем кол-во блоков  создаем массив со значением true
  for (let i = 0; i < offsetArray.length; i++) {
    showNumber[i] = true;
  }

  //Анимация счета цифр
  const swingNumber = (params) => {
    $(params).each(function () {
      $(this)
        .prop('Counter', 0)
        .animate(
          {
            Counter: $(this).attr('data-value'),
          },
          {
            duration: 1200,
            easing: 'swing',
            step: function (now) {
              $(this).text(
                Math.ceil(now)
                  .toString()
                  .replace(/\B(?=(?:\d{3})+(?!\d))/g, ' ')
              );
            },
          }
        );
    });
  };

  $(window).on('scroll resize load', function () {
    let scroll = $(window).scrollTop() + window.innerHeight - 60;

    for (let i = 0; i < offsetArray.length; i++) {
      if (scroll > $(offsetArray[i]).offset().top && showNumber[i]) {
        swingNumber(
          '.advantages__block[data-advantages="' + `${i + 1}` + '"] b'
        );
        $(
          '.advantages__block[data-advantages="' +
            `${i + 1}` +
            '"] .advantages__c2'
        ).addClass('rounded');
        showNumber[i] = false;
      }
    }
  });
};
